<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>更换头像</span>
    </div>
    <div>
      <!-- 图片，用来展示用户选择的头像 -->
      <img
        v-if="!avatar"
        class="the_img"
        src="../../assets/images/avatar.jpg"
        alt=""
      />
      <img v-else class="the_img" :src="avatar" alt="" />

      <!-- 按钮区域 -->
      <div class="btn-box">
        <input
          type="file"
          accept="image/*"
          style="display: none"
          ref="iptRef"
          @change="onFileChange"
        />
        <el-button type="primary" icon="el-icon-plus" @click="chooseImg">
          选择图片
        </el-button>
        <el-button
          type="success"
          icon="el-icon-upload"
          :disabled="avatar === ''"
          @click="uploadFn"
        >
          上传头像
        </el-button>
      </div>
    </div>
  </el-card>
</template>

<script>
import { updateUserAvatarAPI } from '@/api'
export default {
  name: 'UserAvatar',
  data () {
    return {
      avatar: ''// 保存图片链接/base64字符串
    }
  },
  methods: {
    // 选择图片的点击事件
    chooseImg () {
      // 目的：为了让input[type="file"]标签，让它再用js代码来触发它的点击事件（导致它默认行为给一个文件选择窗口）
      // 原因：input[type="file"]标签它是原生标签，样式不太好改
      // 解决：移花接木
      // 使文件选择窗口出现
      this.$refs.iptRef.click()
    },
    // 选择图片确认了
    onFileChange (e) { // e是原生事件对象
      const files = e.target.files// 拿到用户选择的文件数组
      if (files.length === 0) {
        // 证明刚刚文件选择窗口打开了，但是一个文件都没有选择，然后点击了确定关闭选择弹窗
      } else {
        // 证明它选择了文件（默认只能选一个，如果选择多个你需要给input标签加额外的原生属性）
        console.log(files[0])// 打印它选择的文件对象
        // 目标：选择的图片文件，要给到img标签上做纯前端的预览
        // img标签的src值，只能是图片的链接地址（外连接http://开头，图片文件的相对路径）
        // 或者是图片的base64字符串（而且字符串还必须是data:image/png;base64,图片转base64字符串）

        // 解决方法1：文件-->临时链接地址（这个地址只能在js里内存里不能发给后台）
        // 语法：URL.createObjectURL(文件)
        //    返回值：内存临时地址
        // this.avatar = URL.createObjectURL(files[0])

        // 解决方法2：文件-->base64字符串（此字符串可以发给后台）
        // 语法：
        // 1. 创建 FileReader 对象
        const fr = new FileReader()
        // 2. 调用 readAsDataURL 函数，读取文件内容
        fr.readAsDataURL(files[0])
        // 3.onload等待把文件读成base64字符串后会触发onload事件函数
        fr.onload = e => {
          // 4. 通过 e.target.result 获取到读取的结果，值是字符串（base64 格式的字符串）
          this.avatar = e.target.result
        }
      }
    },
    async uploadFn () {
      const { data: res } = await updateUserAvatarAPI(this.avatar)
      console.log(res)
      this.$message.success(res.message)
      // 立刻让vuex里actions（获取用户信息的actions）再请求一次后台更新vuex里的值
      // 把图片信息传给vuex
      this.$store.dispatch('getUserInfoActions')
      console.log(this.$store.state)
    }
  }
}
</script>

<style lang="less" scoped>
.btn-box {
  margin-top: 10px;
}
.preview {
  object-fit: cover;
}
.the_img {
  width: 350px;
  height: 350px;
}
</style>
